.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

textarea {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
}

.controls {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

button, select, input {
  padding: 10px;
}

.buttons-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 10px;
}

.alert {
  background-color: #e0e0e0;
  padding: 10px;
  margin-top: 20px;
  border-radius: 5px;
}